import React from 'react';
import './Navbar.css';
import PropTypes from 'prop-types';

const Navbar = (props) => {
    return (
        <nav className='navbar'>
            <h1>
                <i id='navbar-icon' className={props.icon} />
                {props.title}
            </h1>
        </nav>
    );
};

Navbar.defaultProps = {
    title: 'GitHub Finder',
    icon: 'fa fa-github',
};
Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    icon: PropTypes.string.isRequired,
};

export default Navbar;
