import React from 'react';
import search34 from './search34.gif';

export const Finder = (props) => {
    return (
        <div className='user-container'>
            <img
                src={search34}
                alt='Use Search Bar....'
                style={{
                    width: '200px',
                    margin: 'auto',
                    justifyContent: 'center',
                    textAlign: 'center',
                }}
            />
        </div>
    );
};
