import { React, useState } from 'react';
import PropTypes from 'prop-types';
import { Finder } from '../Layout/Finder';


export default function Search(props) {
    const [text, setText] = useState('');

    const onChangeHandle = (event) => {
        setText(event.target.value);
    };
    const onSubmitHandle = (event) => {
        event.preventDefault();
        // if(text===''){
        //     props.setAlert("Username Field should not be blank",'light')
        // }
        props.searchUsers(text);
        setText('');
    };

    return (
        <>
        <div className='search-console'>
            <form className='form' onSubmit={onSubmitHandle}>
                <input id='username-input'
                    type='text'
                    name='text'
                    value={text}
                    placeholder='Enter Username...'
                    onChange={onChangeHandle}
                />
                <input type='submit' value='Search' className='btn btn-dark' />

                {props.showClear && (
                    <button className='btn btn-dark' onClick={props.clearUsers}>
                        Clear
                    </button>
                )}
            </form>
        </div>
        {props.showFinder && (
            <div>
                <Finder />
            </div>
        )}
        </>
    );
}
Search.propTypes = {
    searchUsers: PropTypes.func,
    onClearHandle: PropTypes.func,
};
