import React from 'react';
import spinner34 from "./spinner34.gif";

export const LoadingSpinner = () => {
    return (
        <div >
            <img
                src={spinner34}
                alt='Loading...'
                style={{ width: '200px', margin: 'auto' ,justifyContent:'center',textAlign:'center'}}
            />
        </div>
    );
};
